
import moment from 'moment'
import util,{ _,by } from '@/libs/util'
import common from '@/libs/common.mixin'
import globals from '@/libs/globals.mixin'
import Card from 'vux/src/components/card/index.vue'

import Progress from 'c%/progress'
import Alert from 'c%/Alert.vue'

export default{
mixins: [common,globals],
components: {
Card,
Alert,
Progress
},
data () {
return {
data: [],
params: {},
noDataText: '您今天还没有排课~'
}
},
mounted () {

},
computed: {
min_before_class () {
let result = 15
if(this.params&&this.params.class_attendance){
result = this.params.class_attendance.min_before_class
}
return result
},
min_after_class () {
let result = 15
if(this.params&&this.params.class_attendance){
result = this.params.class_attendance.min_after_class
}
return result
},
data_uri () {
let uri = util.sprintf('course_arranges?with=class_attendance,one_class&int_day=%s&teach_eid=%s',moment(new Date()).format("YYYYMMDD"),this.eid$)
return uri
}
},
methods: {
attendance (item) {
if(this.isAllowAttendance(item)){
this.$router.push({path:'./schedule/'+item.ca_id})
}else{
this.toast('当前排课已超出点名考勤时间','text')
}
},
isAllowAttendance (item) {

let start_int_hour = util.int_hour_add_minutes(item.int_start_hour,-util.int(this.min_before_class))
let end_int_hour = util.int_hour_add_minutes(item.int_end_hour,this.min_after_class)
let result = util.int_hour_in_section(moment(new Date()).format('HHmm'),start_int_hour,end_int_hour)

return result
},
loadFinished () {
this.getParams()
},

getParams () {
this.$rest('configs?cfg_name=params').get()
.success(data=>{
this.params = data.cfg_value
this.getNearestArrange()
})
},
getTeachPercent (row) {
return (row.attendance_times / row.arrange_times) * 100
},
getNearestArrange () {
let data = this.data.sort(by('int_start_hour',1)),
obj = {},
findFilter = (item)=>{
return item.is_attendance < 2 && this.isAllowAttendance(item)
}

if(data.length > 0){
obj = data.find(findFilter)

if(!_.isEmpty(obj)){
this.$set(obj,'_active',true)
}
}
}
}
}



